<template>
  <form @submit.prevent="onSubmit" class="DiscussionReplyForm">
      <textarea v-model="text" name="description" autoFocus placeholder="Write your question" class="DiscussionReplyForm-area"></textarea>
      <input type="submit" value="send" class="DiscussionReplyForm-submit"/>
  </form>
</template>

<script>
  export default {
    name: 'discussion-add',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        value: '',
        text: ''
      }
    },
     methods: {
        onSubmit() {
          if(this.text != ""){
            var newcomment = {comentario: this.text};
            console.log(newcomment);
            this.$parent.discussions.push(newcomment);
            this.text = ""
          }          
        }
    },
    created() {
      this.active = true
    }
  }
</script>






