<template>
  <div class="Discussion is-first  is-shadow">
      <div class="Discussion-wrapper">
          <div class="Discussion-top">
              <div class="DiscussionAuthor">
                  <span class="DiscussionAuthor-name">
                      <img :src="$parent.avatar" class="DiscussionAuthor-avatar" width="40" height="40"/>
                      <a href="#" title="Ver perfil de usuario" target="_blank" v-text="$parent.name"></a>
                  </span>
              </div>
              <div class="">
                  <span class="Discussion-date">{{ new Date() }}</span>
              </div>
          </div>
          <div class="Discussion-text"><slot name="comentario"></slot></div>
          <div class="Discussion-bottom">
              <div class="Discussion-bottomLeft">
                  <span>
                      <span @click="onUpVote" class="Discussion-vote Discussion-voteUp icon-plus_A "></span>
                      <span @click="onDownVote" class="Discussion-vote Discussion-voteDown icon-minus_A "></span>
                      <span class="Discussion-points">{{ votes }} points</span>
                  </span>
                  <!-- Do the functionality for reply -->
                  <!-- {function(){
                      if(this.props.level < 3){
                          return (
                              <span>
                                  <span onClick={this._onReply} class="icon-reply Discussion-reply">Reply</span>
                                  <span class="Discussion-comments">{this.state.answers.length} answers</span>
                              </span>
                          )
                      }
                  }.bind(this)()} -->
              </div>
              <div class="Discussion-bottomRight">
              </div>
          </div>
      </div>
      <!-- Do the functionality for form reply -->
     <!--  <div class="Discussion-editor">
          {replyForm}
      </div>
      <div class="Discussion-children">
          <CSSTransitionGroup transitionName="Discussion-child">
          {this.state.answers.reverse()}
          </CSSTransitionGroup>
      </div> -->
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        votes: 1,
        answers: [],
        date: moment(this.props.date).fromNow()
      }
    },
     methods: {
        onSubmit() {
          this.active = false;
          this.$parent.showformdiscussion = true;
          console.log(this.$parent.showformdiscussion);
        },
        onUpVote(){
            this.votes = this.votes + 1
        },

        onDownVote(){
          this.votes = this.votes - 1
        },
        onReply(){
           console.log('va a responder')
        }

    },
    created() {
      this.active = true
    }
  }
</script>






