<template>
  <div>
    <form @submit.prevent="onSubmit" class="DiscussionUser" v-if="active">
        <label class="label_A">Como te llamas</label>
        <input 
            type="text" 
            name="name"
            class="input_A"
            placeholder="Write your name"
            v-model="name"
            onChange={this._onChange}
        />
        <label class="label_A">Paste an image url for your avatar</label>
        <input 
            type="text" 
            name="avatar"
            class="input_A"
            placeholder="example : https://pbs.twimg.com/profile_images/480073396072742912/h6VGOhH6.jpeg"
            v-model="avatar"
            onChange={this._onChange}
        />
        <input type="submit" value="Enter" class="btn-Red btn--big"/>
    </form>


    <div class="DiscussionBody" v-if="this.$parent.showformdiscussion">
      <discussion-add>
        
      </discussion-add>
        <div class="Discussion-list" v-for="discusion in discussions">
        
          <discussion-item>
            <span slot="comentario">{{ discusion.comentario }}</span>
          </discussion-item>

        </div>

      <discussion-footer>
      </discussion-footer>
      
    </div>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        name: '',
        avatar: '',
        active: false,
        discussions: []
      }
    },
     methods: {
        onSubmit() {
          this.active = false;
          this.$parent.showformdiscussion = true;
          console.log(this.$parent.showformdiscussion);
          if(this.avatar === ""){
            this.avatar = "https://static.platzi.com/media/avatares/default.png"
          }

          if(this.name === ""){
            name = "Guest"
          }
          alert('enviando los datos ' + this. name)
        }
    },
    created() {
      this.active = true
    }
  }
</script>






